.core-root {
    width: 100vw;
    height: 100vh;
}

.core-root > .wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    overflow-x: hidden;
}

.core-root > .wrapper > .container:nth-of-type(1) { 
    display: flex;
    flex: 15%;
    justify-content: center;
    border-right: 2px solid var(--color-surface);
}
.core-root > .wrapper > .container:nth-of-type(2) { 
    flex: 85%;
    padding: var(--dimen-medium-padding);
}

.core-root > .wrapper > .container:nth-of-type(3) {
    flex: 30%;
    justify-content: center;
}

.navigation-container {
    width: 100%;
    padding: var(--dimen-small-padding);
}

.navigation-container > .header {
    font-weight: 700;
    font-size: 1.6em;
    padding: var(--dimen-medium-padding) 0;
}

.navigation-container .saucepan-add {
    margin: var(--dimen-small-padding) auto;
    padding: 8px 16px 6px;
    background-color: var(--color-primary);
    color: var(--color-on-surface);
    font-weight: 500;
    font-size: 1em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
}

.navigation-container .saucepan-add:hover {
    background-color: var(--color-primary);
    color: var(--color-on-primary);
}

.navigation-container .saucepan-item {
    width: 100%;
    margin: var(--dimen-tiny-padding) 0;
    padding: var(--dimen-small-padding);
    background-color: var(--color-window-background);
    color: var(--color-secondary-text);
    font-weight: 500;
    font-size: 1em;
    white-space: nowrap;
    overflow: hidden;
    text-transform: capitalize;
    text-overflow: ellipsis;
    text-align: start;
}

.navigation-container .saucepan-item:hover {
    color: var(--color-on-surface);
}

.navigation-container .active {
    background-color: var(--color-primary-transparent);
    color: var(--color-primary);
    transition: 0.5s ease-in-out;
}