@import url(https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap);
.container-show {
    visibility: visible;
    opacity: 1;
    transition: visiblity 0s, opacity 0.5s linear;
}
.container-hide {
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s, opacity 0.5s linear;
}

.container > .wrapper {
    height: 100%;
    padding: var(--dimen-medium-padding);
    background-color: var(--color-surface);
    color: var(--color-on-surface);
}

.dismiss-button {
    width: 24px;
    height: 24px;
    padding: 0;
    background-color: transparent;
    float: right;
}

.dismiss-button:hover {
    background-color: var(--color-primary);
}

.dismiss-button > #icon {
    display: block;
    margin: auto;
}

.header {
    font-weight: 700;
    font-size: 1.2em;
    color: var(--color-on-surface);
}

.editor-form > label {
    margin-bottom: 32px;
    font-weight: 500;
    font-size: 0.8em;
    text-transform: uppercase;
}
.sauce-list {
    height: 100%;
    padding: var(--dimen-medium-padding);
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(196px, 1fr));
    grid-template-rows: repeat(auto-fit, 196px);
    grid-gap: var(--dimen-small-padding);
    gap: var(--dimen-small-padding);
    box-sizing: border-box;
}

.sauce-list > .add-action {
    position: relative;
    border-radius: var(--dimen-surface-radius);
    border: 1px solid var(--color-border);
    background-color: var(--color-surface);
    color: var(--color-secondary-text);
    text-transform: uppercase;
}

.sauce-list > .add-action:hover {
    border: 1px solid var(--color-primary);
    transition: 0.5s ease-in-out;
    color: var(--color-primary);
}

.sauce-list > .add-action > .wrapper {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.add-action > .wrapper > #icon {
    font-size: 1.8em;
}

.sauce-container {
    padding: var(--dimen-medium-padding);
    background-color: var(--color-primary);
    color: var(--color-primary);
    border-radius: var(--dimen-surface-radius);
    border: 1px solid var(--color-primary);
    text-align: start;
    overflow: hidden;
}

.sauce-container:hover {
    background-color: var(--color-primary);
}

.sauce-question, .sauce-answer {
    color: var(--color-on-primary);
}

.button-container {
    text-align: right;
}

.button-wrapper {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: inline-block;
}

.button-wrapper > button {
    display: block;
    margin: auto;
    padding: 8px 12px 6px;
    font-size: 1.2em;
    font-weight: 600;
}
.saucepan-root {
    width: 100%;
    height: 100%;
}

.saucepan-root > .wrapper {
    width: 100%;
    height: 100%;
}

.saucepan-root > .wrapper > .header-container:hover .header-edit {
    visibility: visible;
    padding: 0;
}

.saucepan-root > .wrapper > .header-container > .header-wrapper {
    display: inline-block;
}

.saucepan-root > .wrapper > .header-container > .header-wrapper > #header {
    padding: var(--dimen-medium-padding);
    font-weight: 800;
    font-size: 1.8em;
    color: var(--color-primary-text);
}

.saucepan-root > .wrapper > .header-container form {
    display: inline-block;
    margin-right: var(--dimen-medium-padding);
}

.saucepan-root > .wrapper > .header-container .header-edit {
    visibility: hidden;
    padding: 0;
    background-color: transparent;
}

.saucepan-root > .wrapper > .header-container form > #input_submit {
    display: none;
}
.core-root {
    width: 100vw;
    height: 100vh;
}

.core-root > .wrapper {
    width: 100%;
    height: 100%;
    display: flex;
    overflow-x: hidden;
}

.core-root > .wrapper > .container:nth-of-type(1) { 
    display: flex;
    flex: 15% 1;
    justify-content: center;
    border-right: 2px solid var(--color-surface);
}
.core-root > .wrapper > .container:nth-of-type(2) { 
    flex: 85% 1;
    padding: var(--dimen-medium-padding);
}

.core-root > .wrapper > .container:nth-of-type(3) {
    flex: 30% 1;
    justify-content: center;
}

.navigation-container {
    width: 100%;
    padding: var(--dimen-small-padding);
}

.navigation-container > .header {
    font-weight: 700;
    font-size: 1.6em;
    padding: var(--dimen-medium-padding) 0;
}

.navigation-container .saucepan-add {
    margin: var(--dimen-small-padding) auto;
    padding: 8px 16px 6px;
    background-color: var(--color-primary);
    color: var(--color-on-surface);
    font-weight: 500;
    font-size: 1em;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
}

.navigation-container .saucepan-add:hover {
    background-color: var(--color-primary);
    color: var(--color-on-primary);
}

.navigation-container .saucepan-item {
    width: 100%;
    margin: var(--dimen-tiny-padding) 0;
    padding: var(--dimen-small-padding);
    background-color: var(--color-window-background);
    color: var(--color-secondary-text);
    font-weight: 500;
    font-size: 1em;
    white-space: nowrap;
    overflow: hidden;
    text-transform: capitalize;
    text-overflow: ellipsis;
    text-align: start;
}

.navigation-container .saucepan-item:hover {
    color: var(--color-on-surface);
}

.navigation-container .active {
    background-color: var(--color-primary-transparent);
    color: var(--color-primary);
    transition: 0.5s ease-in-out;
}
:root {
	--color-primary: #d32f2f;
	--color-primary-variant: #9a0007;
	--color-primary-transparent: #d32f2f1f;
	--color-on-primary: #ffffff;
	--color-surface:#212121;
	--color-on-surface: #ffffff;
	--color-window-background: #1b1b1b;
	--color-toolbar: var(--color-primary-variant);
	--color-primary-text:#ffffff;
	--color-secondary-text: #aaabb8;
	--color-border: var(--color-secondary-text);

	--dimen-icon-size: 16px;
	--dimen-medium-padding: 16px;
	--dimen-small-padding: 8px;
	--dimen-tiny-padding: 4px;
	--dimen-surface-radius: 4px;
}

* {
    box-sizing: border-box;
}

body {
	margin: 0;
	font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
		'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
		sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #1b1b1b;
	background-color: var(--color-window-background);
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

input {
	width: 100%;
    margin-bottom: 8px;
    margin-bottom: var(--dimen-small-padding);
	padding: 8px;
	padding: var(--dimen-small-padding);
	border-radius: 4px;
	border-radius: var(--dimen-surface-radius);
    border: 1px solid #aaabb8;
    border: 1px solid var(--color-secondary-text);
	outline: none;
    background-color: #1b1b1b;
    background-color: var(--color-window-background);
    color: #ffffff;
    color: var(--color-primary-text);
    font-weight: 400;
	font-size: 1em;
}

input:hover {
	border: 1px solid #d32f2f;
	border: 1px solid var(--color-primary);
}

::-webkit-input-placeholder {
	color: #aaabb8;
	color: var(--color-secondary-text);
}

:-ms-input-placeholder {
	color: #aaabb8;
	color: var(--color-secondary-text);
}

::-ms-input-placeholder {
	color: #aaabb8;
	color: var(--color-secondary-text);
}

::placeholder {
	color: #aaabb8;
	color: var(--color-secondary-text);
}

button {
	border: none;
    outline: none;
    margin-right: 8px;
    margin-right: var(--dimen-small-padding);
	padding: 8px 32px 6px;
	background-color: #d32f2f;
	background-color: var(--color-primary);
    color: #ffffff;
    color: var(--color-on-primary);
	border-radius: 4px;
	border-radius: var(--dimen-surface-radius);
	font-weight: 500;
	font-size: 1em;
	text-transform: uppercase;
}

button:hover {
	cursor: pointer;
	background-color: #9a0007;
	background-color: var(--color-primary-variant);
    transition: 0.5s ease-in-out;
}
